import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DebuggingHelperComponent } from './debugging-helper.component';

const routes: Routes = [
	{
		path: '',
		component: DebuggingHelperComponent,
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class DebuggingHelperRoutingModule {}
